body {
  margin: 0;
  font-family: Arial, sans-serif;
  background-color: #121212;
  color: #f5f5f5;
}

.app {
  text-align: center;
  padding: 20px;
}

h1 {
  margin-bottom: 20px;
  color: #00d1ff;
}

ul {
  list-style: none;
  padding: 0;
}

li {
  background: #1e1e1e;
  margin: 10px auto;
  padding: 10px 20px;
  width: 350px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-radius: 8px;
  box-shadow: 0 2px 5px rgba(0,0,0,0.5);
}

li div button {
  margin-left: 5px;
  padding: 5px 10px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-weight: bold;
}

.update-btn {
  background-color: #00d1ff;
  color: #121212;
}

.delete-btn {
  background-color: #ff4c4c;
  color: #fff;
}

.form {
  margin-top: 20px;
}

input {
  padding: 8px;
  margin: 5px;
  border-radius: 5px;
  border: none;
}

button {
  padding: 8px 15px;
  border-radius: 5px;
  border: none;
  background-color: #00d1ff;
  color: #121212;
  cursor: pointer;
  font-weight: bold;
}

button:hover, .update-btn:hover, .delete-btn:hover {
  opacity: 0.8;
}
